<template>
  <div class="bg">
    <div class="title">我的认证</div>
    <div>
      <Alert class="alert" show-icon v-if="userData.personalStatus == -1">
        您还没有进行个人认证,企业/政府认证必须先通过个人认证
        <span slot="desc">请点击下方按钮,填写个人资料进行认证</span>
      </Alert>
      <Alert class="warning" show-icon v-else-if="userData.personalStatus == 0">
        个人认证正在审核中...
        <Icon type="ios-clock-outline" slot="icon" />
        <span slot="desc">预计将在1-7个工作日内完成审核,如有疑问联系18966276820</span>
