        <span slot="desc">如果你想发布企业/政府项目,还需认证企业/政府</span>
      </Alert>

      <Alert
        class="alert"
        show-icon
        v-else-if="userData.personalStatus == 1  && userData.auditingNow && userData.auditingNow.status == 0"
      >
        {{userData.auditingNow.type ==2 ? '企业认证' : userData.auditingNow.type ==3 ? '政府认证' : '未知认证'}}资料正在审核中...
        <Icon type="ios-clock-outline" slot="icon" />
        <span slot="desc">预计将在1-7个工作日内完成审核,如有疑问联系18966276820</span>
      </Alert>

      <Alert
        class="alert"
        type="error"
        show-icon
        v-else-if="userData.personalStatus == 1  && userData.auditingNow && userData.auditingNow.status == 2"
      >
        非常抱歉,{{userData.auditingNow.type ==2 ? '企业认证' : userData.auditingNow.type ==3 ? '政府认证' : '未知认证'}}未通过
        <Icon type="ios-clock-outline" slot="icon" />
        <span slot="desc">{{userData.auditingNow.fail}},请重新发起认证</span>
      </Alert>

      <Alert
        class="alert"
        show-icon
        v-else-if="userData.personalStatus == 1  && userData.auditingNow && userData.auditingNow.status == 1 && !userData.government"
      >
        您已经通过个人/企业认证
        <span slot="desc">
          如果您有多个企业，请继续发起认证,也可以认证为政府认证
          <br />提醒：升级为政府则需完融资项目及资金,如有疑问联系18966276820
        </span>
      </Alert>

      <Alert
        class="alert"
        show-icon
        v-else-if="userData.personalStatus == 1  && userData.government"
      >
        您已经通过政府认证,可以发布政府项目
        <span slot="desc">当前账号认证为政府认证,将不能发布个人以及企业项目/资金</span>
      </Alert>
    </div>

    <div class="menus_list">
      <div>
        <span class="menus_list_left">
          <img src="../../assets/img/certify-1.png" alt />
        </span>
        <span class="menus_list_right">
          <p>个人认证</p>
          <router-link
            v-if="userData.personalStatus == -1 || userData.personalStatus == 2"
            tag="p"
            to="/user/certify-add"
          >去认证</router-link>
          <p v-else-if="userData.personalStatus == 0 && !userData.government">请等待</p>
          <p class="p_active" v-else-if="userData.personalStatus == 1 && !userData.government">已认证</p>
          <p v-else>失 效</p>
        </span>
      </div>
      <div>
        <span class="menus_list_left">
          <img src="../../assets/img/certify-2.png" alt />
        </span>
        <span class="menus_list_right">
          <p>企业认证</p>
          <p v-if="userData.personalStatus != 1">无资格</p>
          <router-link
            v-else-if="!userData.company && !userData.government "
            tag="p"
            to="/user/certify-add"
          >去认证</router-link>
          <p v-else-if="userData.auditingNow.status == 0">请等待</p>
          <router-link
            v-else-if="userData.company && !userData.government"
            tag="p"
            to="/user/certify-add"
            class="p_active"
          >再认证</router-link>
          <p v-else>失 效</p>
        </span>
      </div>
      <div>
        <span class="menus_list_left">
          <img src="../../assets/img/certify-3.png" alt />
        </span>
        <span class="menus_list_right">
          <p>政府认证</p>
          <p v-if="userData.personalStatus != 1">无资格</p>
          <router-link
            v-else-if="!userData.government"
            tag="p"
            to="/user/certify-add?status=government"
          >去认证</router-link>
          <p v-else-if="userData.auditingNow.status == 0">请等待</p>
          <p v-else class="p_active">已认证</p>
        </span>
      </div>
    </div>
    <div class="title">
      已生效认证
      <Button @click="clickAuditing" size="small" style="float:right;" type="primary">查看认证记录</Button>
    </div>
    <!-- 个人详细资料 -->
    <table class="table" v-if="userData.personal && !userData.government">
      <tr class="tr_head">
        <th>个人认证</th>
        <th>身份证号</th>
        <th>原件</th>
      </tr>
      <tr>
        <td>{{userData.personal.realName}}</td>
        <td>{{nubMosaic(userData.personal.idcard.toString())}}</td>
        <td style="width: 260px;">
          <Button
            @click="clickUrl(userData.personal.identityFront)"
            size="small"
            style="margin-right:10px;"
          >查看身份证正面</Button>
          <Button @click="clickUrl(userData.personal.identityBehind)" size="small">查看身份证反面</Button>
        </td>
      </tr>
    </table>
    <!-- 企业详细资料 -->
    <table class="table" v-if="userData.company">
      <tr class="tr_head">
        <th>企业认证</th>
        <th>授权人</th>
        <th>地址</th>
        <th>原件</th>
      </tr>
      <tr v-for="(item, index) in userData.company" :key="index">
        <td>{{item.companyName}}</td>
        <td>{{item.certigier}}</td>
        <td style="width: 260px;">{{item.address.name}} {{item.detailedAddress}}</td>
        <td style="width: 260px;">
          <Button @click="clickUrl(item.authorization)" size="small" style="margin-right:10px;">授权证书</Button>
          <Button @click="clickUrl(item.licenseFile)" size="small" style="margin-right:10px;">营业执照</Button>
          <Button @click="clickUrl(item.honorCertificate)" size="small">企业荣誉</Button>
        </td>
      </tr>
    </table>
    <!-- 政府详细资料 -->
    <table class="table" v-if="userData.government">
      <tr class="tr_head">
        <th>政府认证</th>
        <th>地址</th>
        <th>原件</th>
      </tr>
      <tr>
        <td>{{userData.government.unitsName}}</td>
        <td>{{userData.government.address.name}}{{userData.government.address.detailedAddress}}</td>
        <td style="width: 260px;">
          <Button
            @click="clickUrl(userData.government.unitsAuthorization)"
            size="small"
            style="margin-right:10px;"
          >单位授权书</Button>
          <Button @click="clickUrl(userData.government.unitsCertificate)" size="small">单位法人证书</Button>
        </td>
      </tr>
    </table>
    <!-- 弹出框开始 -->
    <Modal
      id="model"
      v-model="modalConfig.show"
      :title="modalConfig.title"
      @on-ok="modalConfig.show"
    >
      <div v-if="userAuth.length > 0">
        <p v-for="(item, index) in userAuth" :key="index">
          <a class="title">{{`${item.create_time} `}}</a>
          <br />
          <a
            class="content"
          >{{`认证类型：${item.type == 1 ? '个人认证' : item.type == 2 ? '企业认证' : item.type == 3 ? '政府认证' : '未知参数'} `}}</a>
          <br />
          <a
            class="content"
          >{{`认证结果：${item.status == 0 ? '等待审核' : item.status == 1 ? '审核通过' : item.status == 2 ? '审核失败' :item.status == 3 ? '认证注销' : '未知错误'}`}}</a>
          <br />
          <a class="content" v-if="item.status == 2">{{`原因：${item.fail}`}}</a>
        </p>
      </div>
      <div v-else>
        <p>暂时没有认证记录</p>
      </div>
      <div slot="footer">
        <Button type="info" size="large" long @click="modalConfig.show = false">关闭</Button>
      </div>
    </Modal>
    <!-- 弹出框结束 -->
  </div>
</template>
<script>
import { Alert, Button, Modal, Icon } from "iview";
import { getAuditing, getAuditingStatus } from "../../api/user";
import { imgUrl, nubMosaic } from "../../utils";
export default {
  components: {
    [Alert.name]: Alert,
    [Modal.name]: Modal,
    [Icon.name]: Icon,
    [Button.name]: Button
  },
  data() {
    return {
      imgUrl: imgUrl,
      modalConfig: {
        show: false,
        title: "标题"
      },
      userData: {},
      userAuth: []
    };
  },
  methods: {
    nubMosaic,
    clickUrl(url) {
      window.open(this.imgUrl + url);
    },
    clickAuditing() {
      this.modalConfig = { show: true, title: "认证记录" };
      getAuditing().then(res => {
        this.userAuth = res.data.data;
      });
    }
  },
  created() {
    getAuditingStatus().then(res => {
      this.userData = res.data.data;
      if (this.userData.company) {
        for (const item of this.userData.company) {
          item.address = JSON.parse(item.address);
        }
      }
      if (this.userData.government) {
        this.userData.government.address = JSON.parse(
          this.userData.government.address
        );
      }
    });
  }
};
</script>
